<template>
  <transition name="filters-transition">
    <aside v-if="!hideFilters" class="col-2">
      <v-expansion-panels class="border" flat>
        <v-expansion-panel v-for="(section, x) in Object.keys(sections)" :key="x" accordion>
          <v-expansion-panel-header class="font-weight-bold">
            {{ $t(section) }}
          </v-expansion-panel-header>

          <v-expansion-panel-content>
            <v-checkbox v-for="variant in Object.keys(sections[section])" :key="variant" :label="variant" v-model="selectedElements[section][variant]" hide-details></v-checkbox>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </aside>
  </transition>
</template>

<script>
var _ = require('lodash')

import { mapGetters } from 'vuex'

import fashion from '../../data/fashion.json'

export default {
  name: 'SideFilters',
  
  props: {
    hideFilters: {
      type: Boolean
    }
  },

  data: () => ({
      searchedPrice: [],
      minPrice: 0,
      maxPrice: 150,

      selectedElements: { size: [], color: [], brand: [], material: [], cut: [], season: [], novelties: [], delivery: [] }
  }),

  computed: {
    ...mapGetters(['searchedProducts']),

    sections() {
      return {
        size: this.sizes,
        color: [],
        brand: [],
        material: this.material,
        cut: [],
        season: [],
        novelties: [],
        delivery: []
      }
    },

    sizes() {
      var container = {}
      _.forEach(fashion.sizes, (label) => {
        container[label] = false
      })
      return container
    },

    material() {
      var container = {}
      // TODO: Maybe limit the number of items
      // shown according to the materials present
      // in the backend
      _.forEach(fashion.material, (material) => {
        container[material] = false
      })
      return container
    }
  }
}
</script>

<style scoped>
.filters-transition-enter-active,
.filters-transition-leave-active {
    transition: all .2s ease-in-out;
}

.filters-transition-enter,
.filters-transition-leave-to {
    opacity: 0;
    transform: translateX(-30px);
}

.filters-transition-leave,
.filters-transition-enter-to {
    opacity: 1;
    transform: translateX(0px);
}

.filters-transition-move {
    transition: all .2s ease-in-out;
}
</style>
